.dialog {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(187, 202, 211, .6);
}

.container {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  position: absolute;
  width: 100%;
  max-width: 800px;
  height: 100%;
  max-height: 500px;
  padding: 10px;
  background: #fff;
  border-radius: 2px;
  overflow: scroll;
}



